@import (less) 'color.less';
.menu {
    position: relative;
    width: 100%;
    height: 0px;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: @darkest_gray;
    -moz-transition: height 0.25s ease-out;
    -o-transition: height 0.25s ease-out;
    transition: height 0.25s ease-out;
    .icon {
        margin-right: 0.313em;
        font-size: 1.750em;
        vertical-align: middle;
    }
}

.menu.closed {
    height: 0;
}

.menu.open {
    height: 22em;
}

.main-menu-container {
    padding: 0;
}

.section-menu:nth-child(2n+3) {
    clear: left;
}

.main-menu {
    list-style: none;
    padding: 0 0.313em 0 0.313em;
    margin: 0;
    a {
        color: @light_gray;
        display: table;
        width: 100%;
        padding: 0.313em 1.250em 0.313em 1.250em;
        margin: 1.250em 0 1.250em 0;
        text-decoration: none;
        cursor: pointer;
        height: 3.750em; // border: 1px solid @gray;
        // border-radius: 2px;
        &:hover,
        &:focus,
        &:active {
            background-color: @green;
            color: @white; // border: 1px solid @green;
        }
        span {
            display: table-cell;
            vertical-align: middle;
            text-align: left;
            &:nth-child(1) {
                width: 1.563em;
            }
        }
    }
    .select {
        background-color: @green;
        color: @white;
        border: 1px solid @green;
    }
}

.upper-bar {
    border-bottom: 1px solid @gray;
}

.menu-principale {
    color: @light_gray;
    font-size: 0.875em;
    padding: 1em 0.625em 0.625em 0.625em;
    cursor: pointer;
    .icon {
        font-size: 0.875em;
    }
}

.menu-principale-title {
    vertical-align: top;
}

.selected-first-level-menu {
    color: @gray;
    padding: 0.625em;
}

.first-level-menu {
    position: absolute;
    left: 0;
    right: 0;
    opacity: 0;
    left: -6.250em;
    padding: 0 0.625em 0 0.625em;
    -moz-transition: all 0.25s ease-out;
    -o-transition: all 0.25s ease-out;
    transition: all 0.25s ease-out;
}

.first-level-menu.open {
    opacity: 1;
    left: 0;
}

.second-level-menu {
    position: absolute;
    left: 0;
    right: 0;
    opacity: 0;
    left: 6.250em;
    -moz-transition: all 0.25s ease-out;
    -o-transition: all 0.25s ease-out;
    transition: all 0.25s ease-out;
}

.second-level-menu.open {
    opacity: 1;
    left: 0;
}

//
@media (min-width: 768px) {
    overflow-y: auto;
}

.contrasto-on {
    .menu {
        background-color: @black;
        &.open {
            border-bottom: 2px solid white;
        }
    }
    .main-menu {
        a {
            color: @white;
            border: 1px solid @white;
            &:hover,
            &:focus,
            &:active {
                background-color: @white;
                color: @black;
                border: 1px solid @white;
            }
        }
        .select {
            background-color: @white;
            color: @black;
            border: 1px solid @white;
        }
    }
    .upper-bar {
        border-bottom: 1px solid @black;
    }
    .menu-principale {
        color: @white;
    }
    .selected-first-level-menu {
        color: @white;
    }
    .user-button {
        a {
            color: @white;
            border: 1px solid @white;
            &:hover,
            &:focus,
            &:active {
                background-color: @white;
                color: @black;
                border: 1px solid @white;
            }
        }
    }
}